.container-details {
    color: #fff;
    margin: 50px 100px 50px 100px;
    background-color: #0f1014;
    min-height: 500px;
}

.movie-details {
    display: flex;
    align-items: center;
    justify-content: center;
}

.image-details {
    margin-top: 50px;
    width: 350px;
    height: 400px;
    border-radius: 1rem;
}

.details {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-left: 50px;
    max-width: 40%;
}

.title-details {
    margin-bottom: 2rem;
    margin-top: 2rem;
}

.sinopse {
    line-height: 120%;
    font-size: 120%;
    margin-bottom: 1rem;
}

.release-date {
    opacity: 0.5;
    margin-bottom: 1rem;
}

p {
    margin-bottom: 1rem;
}

p svg {
    color: rgb(250, 250, 17);
}

.open-trailer-button {
    background-color: #fff;
    border: none;
    cursor: pointer;
    border-radius: 1rem;
    padding: 0.8rem 2rem;
    font-size: 100%;
    transition: all 0.3s;
    display: flex;
    justify-content: center;
    align-items: center;
    color:black;
}

.open-trailer-button:hover {
    background-color: rgb(235, 29, 80);
    color: #fff;
}