.container-nav-bar {
    display: flex;
    background-color: #0f1014;
    width: 100%;
    height: 50px;
    align-items: center;
    justify-content: space-between;
}

.container-options {
    text-decoration: none;
}

.container-title {
    font-size: 30px;
    margin-left: 40px;
    color: rgb(235, 29, 80);
}

.subtitles {
    color: #fff;
    cursor: pointer;
    margin-left: 40px;
    font-size: 15px;
    text-transform: uppercase;
    transition: all 0.3s;
}

.subtitles:hover {
    color: rgb(235, 29, 80);
}

.active-link {
    color: rgb(235, 29, 80);
}

form {
    display: flex;
    flex-direction: center;
    align-items: center;
}

input {
    position: relative;
    width: 300px;
    height: 30px;
    border: none;
    padding-left: 10px;
    font-size: 15px;
    border-radius: 40px 0 0 40px;
    background-color: #fff;
    outline: none;
}

.nav-button {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(235, 29, 80);
    color: #fff;
    height: 30px;
    width: 30px;
    border: none;
    margin-right: 50px;
    cursor: pointer;
    border-radius: 0 40px 40px 0;
    outline: none;
}